.u-mgnt .user-detail-link:hover {
    color: rgb(64, 211, 35);
    text-decoration: underline;
    cursor: pointer;
}

.detail-tbody input {
    border: 1px solid white;
    background-color: transparent;
}
.mgnt-table{
    width: 920px;
    margin-left: -90px;
}
.mgnt-title {
    float:left;
    width: 920px;
    text-align: left;
    margin-left: -100px;
    font-size:30px;
}
.mgnt-btnbox {
    width: 828px;
    display: inline-block;
}
.mgnt-btnbox button {
    float: right;
    margin-left: 20px;
    height: 30px;
    background-color: white;
    border-radius: 4px;
    border:none;
    font-size:15px;
    font-weight: bold;
}
.mgnt-btnbox button:hover {
    background-color: lightgray;
    color:white;
}
.mgnt-name {
    width: 90px;
}
.mgnt-rank {
    width: 100px;
}
.mgnt-call {
    width: 150px;
}
.mgnt-addr {
    width: 330px;
}
.mgnt-exit {
    width:170px
}
.mgnt-date {
    width: 140px;
}
.mgnt-name,
.mgnt-rank,
.mgnt-call,
.mgnt-addr,
.mgnt-date,
.mgnt-yyyy {
    font-size:15px;
    font-weight: bold;
    background-color: #006934;
    color:white;
}